html {
  background-color: black;
  padding-top: 20px;
  padding-bottom: 20px;
}

.functionView {
  display: flex;
  gap: 20px;
  padding-left: 50px;
  align-items: center;
}

.addressInput {
  width: 400px;
  color: white;
  background-color: black;
  padding: 10px;
  border-radius: 8px;

}

.intInput {
  width: 100px;
  color: white;
  background-color: black;
  padding: 10px;
  border-radius: 8px;

}

.functionViewButton {
  background-color: aquamarine;
}

.functionSendButton {
  background-color: orange;
}

p {
  color: white;
  margin: 0;
}

button {
  padding: 10px;
  border-radius: 8px;
  border: none;
}

.App {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

#contractLink {
  color: white;

}

#header {
  padding: 0px 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.linkAddress {
  color: white;
}

#balance {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding-left: 50px;
}